import styled, { keyframes } from "styled-components";

import {
	colors, displayFontSizes, spacingMap, widthSizes,
} from "../styleguide";

import { StyledTooltipProps, TooltipPosition } from "./Tooltip.types";


export const tooltipPositions: Record<TooltipPosition, string> = {
	"top-start": `
		bottom: 100%;
		margin-bottom: ${spacingMap.sm};
	`,
	top: `
		bottom: 100%;
		right: 50%;
		transform: translate(50%, 0);
		margin-bottom: ${spacingMap.sm};
	`,
	"top-end": `
		bottom: 100%;
		right: 0;
		margin-bottom: ${spacingMap.sm};
	`,
	"bottom-start": `
		left: 0;
		margin-top: ${spacingMap.sm};
	`,
	bottom: `
		right: 50%;
		transform: translate(50%, 0);
		margin-top: ${spacingMap.sm};
	`,
	"bottom-end": `
		right: 0;
		margin-top: ${spacingMap.sm};
	`,
	"left-start": `
		right: 100%;
		top: 0;
		margin-right: ${spacingMap.sm};
	`,
	left: `
		right: 100%;
		transform: translate(0, 50%);
		bottom: 50%;
		margin-right: ${spacingMap.sm};
	`,
	"left-end": `
		right: 100%;
		bottom: 0;
		margin-right: ${spacingMap.sm};
	`,
	"right-start": `
		left: 100%;
		top: 0;
		margin-left: ${spacingMap.sm};
	`,
	right: `
		left: 100%;
		transform: translate(0, 50%);
		bottom: 50%;
		margin-left: ${spacingMap.sm};
	`,
	"right-end": `
		left: 100%;
		bottom: 0;
		margin-left: ${spacingMap.sm};
	`,
};

export const animation = keyframes`
	from {
		opacity: 0.6;
	}

	to {
		opacity: 1;
	}
`;

export const TooltipWrapper = styled.div`
	position: relative;
	display: inline-block;
`;
export const TooltipContainer = styled.div<StyledTooltipProps>`
	position: relative;
	display: inline-flex;
	max-width: max-content;
	cursor: pointer;
`;

export const StyledTooltip = styled.div<StyledTooltipProps>`
	position: absolute;
	z-index: ${({ zIndex }) => zIndex};
	color: ${colors.white};
	text-align: left;
	background-color: ${colors.black};
	border-radius: 4px;
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.08),
	0 4px 4px rgba(0, 0, 0, 0.08),
	0 8px 8px rgba(0, 0, 0, 0.08),
	0 16px 32px rgba(0, 0, 0, 0.08);
	${({ isLong }) => isLong && `min-width: ${(widthSizes.sr)}`};
	animation: ${animation} 0.2s linear;
	padding: ${spacingMap.xxs} ${spacingMap.xs};
	box-sizing: border-box;
	${({ position }) => position && tooltipPositions[position]};
`;

export const TooltipText = styled.p<StyledTooltipProps>`
	width: ${widthSizes.full};
	font-family:  ${displayFontSizes.caption.$fontFamily};
	font-size: ${displayFontSizes.caption.$fontSize};
	line-height: ${displayFontSizes.caption.lineHeight};
	margin: 0;
	${({ isLong }) => !isLong && "white-space: nowrap"};
`;
